import * as Actions from '../actions';

const initialState = {
  orderInCheckout: null,
};

const cart = (state = initialState, action) => {
  switch (action.type) {
    case Actions.ADD_SELLER_ORDER_TO_CHECKOUT: {
      return {
        ...state,
        orderInCheckout: action.payload,
      };
    }
    case Actions.REMOVE_SELLER_ORDER_FROM_CHECKOUT: {
      return {
        ...state,
        orderInCheckout: null,
      };
    }
    default: {
      return state;
    }
  }
};

export default cart;
