export const ADD_SELLER_ORDER_TO_CHECKOUT =
  '[CART] ADD_SELLER_ORDER_TO_CHECKOUT';
export const REMOVE_SELLER_ORDER_FROM_CHECKOUT =
  '[CART] REMOVE SELLER ORDER FROM CHECKOUT';

export const sellerOrderToCheckout = (sellerId) => {
  return {
    type: ADD_SELLER_ORDER_TO_CHECKOUT,
    payload: sellerId,
  };
};

export const removeOrderFromCheckout = () => {
  return {
    type: REMOVE_SELLER_ORDER_FROM_CHECKOUT,
  };
};
